import { APG } from "@underlay/apg"
import { rdf, xsd } from "n3.ts"

export const defaultTypes: Record<string, APG.Type> = {
	unit: APG.product({}),
	uri: APG.uri(),

	string: APG.literal(xsd.string),
	boolean: APG.literal(xsd.boolean),
	integer: APG.literal(xsd.integer),
	double: APG.literal(xsd.double),
	date: APG.literal(xsd.date),
	dateTime: APG.literal(xsd.dateTime),
	base64Binary: APG.literal(xsd.base64Binary),
	JSON: APG.literal(rdf.JSON),
}

export const defaultNamespaces: Record<string, string> = {
	ul: "http://underlay.org/ns/",
	xsd: "http://www.w3.org/2001/XMLSchema#",
	rdf: "http://www.w3.org/1999/02/22-rdf-syntax-ns#",
}
